import None from "@/components/qy/none.vue"
export default {
	data() {
		return {
			event_list:'',
			list_empty: false,
			get_img:this.$getimg
		}
	},
	components:{
		None
	},
	onLoad() {
		this.get_event()
	},
	methods: {
		get_event(){
			this.$api.http.get('article/type_article?type=4').then(res=>{
				
				if (res.data=='') {
					this.list_empty = true
				} else {
					this.event_list = res.data
				}
			})
		},
		jump_article(id){
			uni.navigateTo({
				url: '/pages/article/article?id='+id
			});
		}
	},
	onPullDownRefresh() {
		this.get_event()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
